import React from 'react';
import './Template.css';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import PasswordField from 'components/Input/PasswordField';
import TextField from 'components/Input/TextField';
import BlueButton from 'components/Button/BlueButton';
import { GoogleLogin, useGoogleLogin, hasGrantedAllScopesGoogle } from '@react-oauth/google';
Template.propTypes = {
  title: PropTypes.string,
  submitLabel: PropTypes.string,
  onSubmit: PropTypes.func,
  form: PropTypes.array,
  footer: PropTypes.object,
  onClickGmail: PropTypes.func,
  onClickApple: PropTypes.func,
  checkbox: PropTypes.object,
  disabled: PropTypes.any,
  error: PropTypes.any,
  isDisabledButton: PropTypes.any,
  isLoading: PropTypes.bool,
};

function Template(props) {
  const {
    isDisabledButton,
    error,
    disabled,
    title,
    submitLabel,
    onSubmit,
    form,
    footer,
    onClickGmail = () => {},
    onClickApple,
    checkbox,
    isLoading,
  } = props;
  const login = useGoogleLogin({
    onSuccess: (codeResponse) => onClickGmail(codeResponse),
    onError: () => {
      console.log('Login Failed');
    },
  });
  return (
    <div className="auth-page__background">
      <div className="glow-point-left" />
      <div className="glow-point-right" />
      <img src="/assets/img/login-star.svg" className="star one" />
      <img src="/assets/img/login-star.svg" className="star two" />
      <img src="/assets/img/login-star.svg" className="star three" />

      <div className="auth-form__container">
        <div className="auth-form">
          <div className="auth-form__logo">
            <Link to={'#'}>
              <img src="/assets/img/logo.svg" alt="" /> PropSelect
            </Link>
          </div>
          <p className="auth-form__title">{title}</p>

          <div className="auth-form__content">
            <div className="auth-form__divider" />
            <div className="auth-form__text-fields">
              {form.map((item, index) => {
                // Todo: create reuse component Input and PasswordInput
                return item.type === 'password' ? (
                  <PasswordField
                    key={index}
                    placeholder={item.placeholder}
                    disabled={disabled}
                    onChange={item.onChange}
                  />
                ) : (
                  <TextField key={index} placeholder={item.placeholder} disabled={disabled} onChange={item.onChange} />
                );
              })}
            </div>
            <div className="auth-form__button-container">
              <GoogleLogin
                onSuccess={(credentialResponse) => {
                  onClickGmail(credentialResponse);
                }}
                onError={() => {
                  console.log('Login Failed');
                }}
                useOneTap
              />
              {/* <div className="btn btn-dark auth-form__gmail" onClick={login}>
                <img src="/assets/img/gmail.png" /> Gmail
                <i className="fab fa-gmail" />
              </div> */}
            </div>

            {checkbox && (
              <div className="auth-form__checkbox">
                <input type="checkbox" id="checkbox-auth" onChange={checkbox.handleChange} />
                <label htmlFor="checkbox-auth">{checkbox.label}</label>
              </div>
            )}

            <BlueButton isLoading={isLoading} disabled={disabled || isDisabledButton} onClick={onSubmit}>
              {submitLabel}
            </BlueButton>
            {!!error && <small className="text-danger">{error}</small>}

            <div className="auth-form__divider" />
            <div className="auth-form__footer">
              <p>{footer.text}</p>
              <div>
                <Link to={footer.to}>{footer.linkLabel}</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Template;
