import React, { useState } from 'react';
import Template from './Template';
import Client from 'rest_client/baseClient';
import { useNavigate } from 'react-router';
import { useDispatch } from 'react-redux';
import { handleRequest } from 'redux/actions/handleRequestUtils';
import { updateData } from 'redux/reducers/user';
import { createCookie } from 'utils/cookies';
import Constants from 'utils/Constants';
import { login, resendEmailVerify, loginGmail } from 'rest_client/apiClient';
function Register() {
  const http = new Client();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [data, setData] = useState({
    username: '',
    email_address: '',
    password: '',
    agree_receive_emails: false,
  });
  const dispatch = useDispatch();
  const submit = async () => {
    setError('');
    setLoading(true);
    dispatch(
      handleRequest({
        requestClient: () => {
          return http.post('api/v1/auth/register', data);
        },
        errorCallback: (err) => {
          setError(err);
        },
        callback: () => {
          setLoading(false);
        },
        successCallback: () => {
          navigate('/sign-up');
        },
        alertSuccess: 'Register success, please check your mail to verify account.',
      })
    );
  };
  const isDisabledButton = () => {
    return !data.username || !data.email_address || !data.password || !data.agree_receive_emails;
  };
  const _loginGmail = (data) => {
    setLoading(true);
    dispatch(
      handleRequest({
        requestClient: loginGmail,
        data,
        callback: () => setLoading(false),
        showError: true,
        successCallback: (data) => {
          dispatch(updateData(data.data));
          createCookie(Constants.ACCESS_TOKEN, data.accessToken);
          navigate('/');
        },
      })
    );
  };
  return (
    <Template
      title="Sign Up"
      submitLabel="Register"
      isLoading={loading}
      isDisabledButton={isDisabledButton()}
      onSubmit={submit}
      error={error}
      onClickGmail={_loginGmail}
      onClickApple={() => {
        alert('apple');
      }}
      form={[
        {
          name: 'username',
          placeholder: 'Username',
          value: data.username,
          onChange: (e) => {
            setData({ ...data, username: e.target.value });
          },
        },
        {
          name: 'email',
          placeholder: 'Email',
          value: data.email,
          onChange: (e) => {
            setData({ ...data, email_address: e.target.value });
          },
        },
        {
          name: 'password',
          placeholder: 'Password',
          value: data.password,
          type: 'password',
          onChange: (e) => {
            setData({ ...data, password: e.target.value });
          },
        },
      ]}
      footer={{
        text: `Have an account?`,
        linkLabel: 'Sign In',
        to: '/sign-in',
      }}
      checkbox={{
        handleChange: (e) => {
          setData({ ...data, agree_receive_emails: e.target.checked });
        },
        label: `I agree to receive emails from SmartProp Trader & I've Read and accept the Terms & Conditions.`,
      }}
    />
  );
}

export default Register;
